  import Scroll from "../../base/scroll/scroll.vue"
  import Step from "../../base/step/step.vue"
  import Upload from "../../base/upload/upload.vue"

  export default {
    name: "getPrice1",
    components: {
      Step,
      Upload,
      Scroll,

    },
    data() {
      return {
        stepArr: ["", ""],
        stepIndex: 0,
        isOk: true, //
        twoInfo:{},
        goodsImg: [],
        text:'',
        keyWord:''
      }
    },
    //    
    created(){
      let that = this;
//      
      this.$axios.post('/api/orderOperate/getAdditionalInfo', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId
      }).then((res) => {
        let data = res.data
        if (data.code=== "200") {
          this.twoInfo=data.data
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title:'',
          content: '',
        })
      })
    },
//    
    methods: {
      next(){
        console.log(this.text)
        if (this.text!==''&& this.goodsImg.length>2) {
          this.$router.push({ name:'preAppendTextImgFav2',query:{text:this.text,goodImg:this.goodImg, buyerTaskRecordId: this.$route.query.buyerTaskRecordId} })
        }else{
          this.$vux.alert.show({
            title:'',
            content: '',
          })
        }
      }

    }
  }



